<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input>
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!---->
      <el-table
        :data="orderList"
        border
        stripe
        style="width: 100%">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column
          prop="order_number"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="order_price"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="pay_status"
          label="">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status ==='1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="is_send"
          label="">
        </el-table-column>
        <el-table-column
          prop="create_time"
          label="">
          <template slot-scope="scope">
            {{scope.row.create_time|dataFormat}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="showBox"></el-button>
            <el-button type="success" size="mini" icon="el-icon-location-outline" @click="showProgressBox"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <!---->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader :options="citydata"
                       v-model="addressForm.address1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addressDialogClosed"> </el-button>
    <el-button type="primary" @click="dialogVisible = false"> </el-button>
  </span>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      :visible.sync="progressVisible"
      width="50%">
      <!---->
      <!--progressInfo-->
      <!--timestamp-->
      <!--activity.context-->
      <el-timeline >
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>

  import citydata from "./citydata";

  export default {
    data() {
      return {
        queryInfo: {
          query: '',
          pagenum: 1,
          pagesize: 10
        },
        total: 0,
        // 
        orderList: [],
        // 
        addressVisible: false,
        addressForm: {
          address1: [],
          address2: ''
        },
        addressFormRules: {
          address1: [
            {required: true, message: '', trigger: 'blur'}
          ],
          address2: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        citydata: citydata,
        progressVisible: false,
        // 
        progressInfo:[]
      }
    },
    created() {
      this.getOrderList()
    },
    methods: {
      async getOrderList() {
        const {data: res} = await this.$http.get(`orders`, {params: this.queryInfo})
        console.log('res:', res)
        if (200 !== res.meta.status) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.orderList = res.data.goods
          this.total = res.data.total
        }
      },
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getOrderList()

      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getOrderList()
      },
      // 
      showBox() {
        this.addressVisible = true
      },
      addressDialogClosed() {
        this.addressVisible = false
        this.addressForm.address1 = []
        this.$refs.addressFormRef.resetFields()
      },
      async showProgressBox() {
        const {data: res} = await this.$http.get(`/kuaidi/1106975712662`)
        if (200!== res.meta.status) {
          return this.$message.error('')
        } else{
          this.$message.success('')
          this.progressInfo= res.data
          this.progressVisible = true
          console.log('progressInfo:',this.progressInfo)
        }
      }
    },
    computed: {},
    name: "order"
  }
</script>


<style lang="less" scoped>

  .el-cascader {
    width: 100%;
  }

</style>
